const all = [...red, ...green, ...blue];

function getColorName({ r, g, b }, length = 5) {
    all.sort((colorA, colorB) => {
        let sumA = Math.abs(r - colorA.RGB[0]) + Math.abs(g - colorA.RGB[1]) + Math.abs(b - colorA.RGB[2])
        let sumB = Math.abs(r - colorB.RGB[0]) + Math.abs(g - colorB.RGB[1]) + Math.abs(b - colorB.RGB[2])
        return (sumA - sumB)
    })

    return all.slice(0, length)
}

